@mixin _420 {
  @media (max-width: 420px) {
    @content;
  }
}
* {
  color: white;
}

.container {
  min-height: calc(100vh - 80px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  contain: layout;
  .ellipse {
    width: calc(100vh - 80px);
    height: calc(100vh - 80px);
    shape-outside: circle();
    border-radius: 50%;
    background: linear-gradient(90deg, #fb69b2 0%, #4646f6 100%);
    opacity: 0.55;
    filter: blur(120px);
    transform: rotate(-180deg);
    &:first-child {
      margin-left: calc(240px - 100vh);
    }
    &:last-child {
      margin-right: calc(240px - 100vh);
    }
  }
  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 24px;
    .create {
      margin: 0 auto;
      padding: 20px 40px;
      width: 355px;
      height: 162px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      background: rgba(29, 25, 37, 0.75);
      backdrop-filter: blur(11px);
      transition: opacity 0.6s, visibility 0.5s, transform 0.3s ease;
      h1 {
        font-weight: 500;
        font-size: 30px;
        line-height: 36px;
        margin: 0 auto;
        @include _420 {
          font-size: 24px;
          line-height: 30px;
        }
      }
      button {
        padding: 12px 11px;
        background: linear-gradient(90deg, #fb69b2 0%, #4646f6 100%);
        box-shadow: 0px 0px 28px rgba(159, 88, 213, 0.44);
        border-radius: 4px;
        &:hover {
          background: linear-gradient(90deg, #d75596 0%, #3838cd 100%);
          box-shadow: 0;
        }
      }
      @include _420 {
        width: 300px;
        height: 150px;
      }
    }
    .info {
      width: 498px;
      margin: 0 auto;
      padding: 20px 40px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      gap: 40px;
      background: rgba(29, 25, 37, 0.75);
      backdrop-filter: blur(11px);
      transition: opacity 0.6s, visibility 0.5s, transform 0.3s ease;
      h1 {
        font-weight: 500;
        font-size: 24px;
        line-height: 28px;
        margin: 0 auto;
      }
      .infoText {
        font-weight: 400;
        font-size: 15px;
        line-height: 160%;
        text-align: center;
        color: #7a7b7c;
      }
      .words {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 8px;
        .word {
          background: rgba(15, 12, 23, 1);
          border-radius: 6px;
          display: flex;
          gap: 6px;
          height: 30px;
          align-items: center;
          justify-content: start;
          padding-left: 2px;
          padding-right: 12px;
          .number {
            background: #191622;
            backdrop-filter: blur(11px);
            border-radius: 4px;
            width: 23px;
            height: 24px;
            display: flex;
            justify-content: center;
            align-items: center;
          }
          input {
            background: rgba(15, 12, 23, 1);
            color: #7a7b7c;
          }
        }
      }
      .buttons {
        width: 100%;
        display: flex;
        justify-content: space-between;
        gap: 12px;
        .understand {
          padding: 12px 11px;
          background: linear-gradient(90deg, #fb69b2 0%, #4646f6 100%);
          box-shadow: 0px 0px 28px rgba(159, 88, 213, 0.44);
          border-radius: 4px;
          flex-grow: 1;
          &:hover {
            background: linear-gradient(90deg, #d75596 0%, #3838cd 100%);
            box-shadow: 0;
          }
        }
        .copy {
          width: 42px;
          height: 42px;
          background: #272332;
          border-radius: 4px;
        }
      }
    }
    .restore {
      font-size: 15px;
      line-height: 18px;
      color: #7a7b7c;
      cursor: pointer;
    }
  }
}

.animation_start {
  visibility: hidden;
  opacity: 0;
  transform: translateY(-20%);
}
.animation {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}
.animation_end {
  visibility: hidden;
  opacity: 0;
  transform: translateY(20%);
}
